.post{
    display: flex;
    flex-direction: column;
    padding: 10px;
    background-color: white;
    border: 1px solid lightgrey;
    border-radius: 5px;
    margin-top: 10px;
}
.post_info{
    display: flex;
    align-items: center;
}
.post_info > h5{
    margin-left: 10px;
    cursor: pointer;
    font-size: 13px;
}
.post_info > h5:hover{
    text-decoration: underline;
}
.post_info > small{
    margin-left: 10px;
}
.post_body{
    display: flex;
    flex-direction: column;
}
.post_body > .post_question{
     margin-top: 10px;
     font-weight: bold;
     margin-bottom: 10px;
     cursor: pointer;
     display: flex;
}
.post_body > .post_question > p:hover{
    text-decoration: underline;
}
.post_body > img{
    object-fit: contain;
    width: 100%;
    border-radius: 5px;
    cursor: pointer;
}
.post_question > .post_btnAnswer{
    margin-left: auto;
    outline: none;
    border: none;
    background-color: blue;
    color: white;
    border-radius: 30px;
    padding: 10px;
}
.post_footer{
    display: flex;
    align-items: center;
    margin-top: 10px;
}
.post_footer > .MuiSvgIcon-root{
    color: gray;
    margin-right: 40px;
    cursor: pointer;
}
.post_footerAction{
    display: flex;
    justify-content: space-around;
    border-radius: 33px;
    padding: 5px;
    background-color: lightgrey;
    align-items: center;
}
.post_footerAction > .MuiSvgIcon-root{
    color: gray;
    margin-right: 40px;
    cursor: pointer;
}
.post_footerAction > .MuiSvgIcon-root:hover{
color: blue;
margin-right: 40px;
cursor: pointer;
}




.post_footerLeft{
    margin-left: auto;
}
.post_footerLeft > .MuiSvgIcon-root{
    color: gray;
    cursor: pointer;
    margin-left: 30px;
}
.modal_question {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  .modal_question > h1 {
    color: blue;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  .modal_question > p {
    color: gray;
    font-size: small;
  }
  
  .modal_question > p > .name {
    color: black;
    font-weight: bold;
  }
  
  .modal_answer {
    display: flex;
    padding-top: 20px;
    flex: 1;
  }
  
  .modal_answer > textarea {
    width: 100%;
    height: 200px;
    padding: 5px;
    font-size: 15px;
    color: blue;
  }
  
  .modal_button {
    display: flex;
    align-items: center;
    margin-right: -20px;
    border-top: 1px solid lightgray;
  }
  
  .modal_button > .cancle {
    margin-left: auto;
    border: none;
    margin-top: 5px;
    outline: none;
    color: rgb(175, 173, 173);
    font-weight: 500;
    padding: 10px;
    margin-right: 10px;
    border-radius: 33px;
    cursor: pointer;
  }
  .modal_button > .add {
    border: none;
    outline: none;
    margin-top: 5px;
    background-color: rgb(0, 64, 255);
    color: white;
    font-weight: 700;
    padding: 10px;
    margin-right: 10px;
    border-radius: 33px;
    cursor: pointer;
  }
  .modal_button > .cancle:hover {
    color: gray;
 }
 .modal_button > .add:hover {
      color: rgb(146, 146, 238);
 }
