@import url(https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap);
*{
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    margin: 0;
    background-color: rgb(241,241,241);
    height: auto;
    width: auto;
}

.login {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: url("https://cdn.pixabay.com/photo/2020/09/21/23/31/car-5591460__340.jpg") no-repeat center center fixed;
  background-size: cover;
}

.login__container {
  display: flex;
  flex-direction: column;
  padding: 20px;
  width: 550px;
  background-color: rgba(255, 255, 255, 0.596);
  /* opacity: 0.5; */
  border-radius: 10px;
  /* align-items: center; */
}

.login__logo {
  display: flex;
  justify-content: center;
  height: 60px;
}
.login__logo > img{
  width:70px;
  /* height: 60px; */
}
.login__desc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 10px;
}

.login__desc > h3 {
  /* margin-top: 10px; */
  text-align: center;
  font-size: xx-large;
  font-family: 'Open Sans', 'Helvetica Neue', sans-serif;
}
.login__desc > p {
  color: gray;
  text-align: center;
  font-weight: 500;
  font-size: 20px;
}

.login__auth {
  display: flex;
  align-items: center;
  margin-top: 8px;
}

.login__authOptions {
  display: flex;
  flex: 0.5 1;
  flex-direction: column;
  padding: 20px;
  border-right: 1px solid lightgray;
}

.login__authDesc {
  display: flex;
  align-items: center;
  padding: 7px;
  margin-bottom: 15px;
}
.login__authDesc > p {
  font-size: small;
  color: gray;
}

.login__emailPass {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  flex: 1.0 1;
  margin-left: 20px;
}

.login__label > h4 {
  padding: 5px;
}

.login__inputFields {
  display: flex;
  flex-direction: column;
  padding: 5px;
}

.login__inputField {
  padding: 5px;
}

.login__inputField > input {
  padding: 10px;
  width: 100%;
  outline: none;
  border: none;
  background-color: rgb(236, 236, 236);
}

.login__forgButt {
  padding-left: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.login__forgButt > small {
  color: gray;
  cursor: pointer;
}

.login__forgButt > small:hover {
  color: blue;
  text-decoration: underline;
}

.login__forgButt > button {
  padding: 9px;
  border-radius: 5px;
  border: none;
  outline: none;
  background-color: blue;
  color: white;
  font-weight: bold;
  font-size: 15px;
  cursor: pointer;
  width: 150px;
}

.login__emailPass > button {
  padding: 9px;
  border-radius: 5px;
  border: none;
  outline: none;
  background-color: blue;
  color: white;
  font-weight: bold;
  font-size: 15px;
  cursor: pointer;
  margin-top: 10px;
}

.login__lang {
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
  margin-top: 30px;
  padding: 10px;
}

.login__lang > p {
  color: gray;
  margin-right: 5px;
  cursor: pointer;
  font-size: 13px;
}

.login__lang > p:hover {
  color: gray;
  margin-right: 5px;
  text-decoration: underline;
}

.login__lang > .MuiSvgIcon-root {
  color: gray;
}

.login__footer {
  padding-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.login__footer > p {
  color: gray;
  font-size: small;
  cursor: pointer;
}

.login__footer > p:hover {
  text-decoration: underline;
}

.reg_btn{
  width: 150px;
  
}

/* .reg_btn_main{
  float:right;
} */
.quora_content{
    display: flex;
    justify-content: center;
    margin-top: 50px;
}
.feed{
    display: flex;
    flex-direction: column;
    flex: 0.6 1;
}
.post{
    display: flex;
    flex-direction: column;
    padding: 10px;
    background-color: white;
    border: 1px solid lightgrey;
    border-radius: 5px;
    margin-top: 10px;
}
.post_info{
    display: flex;
    align-items: center;
}
.post_info > h5{
    margin-left: 10px;
    cursor: pointer;
    font-size: 13px;
}
.post_info > h5:hover{
    text-decoration: underline;
}
.post_info > small{
    margin-left: 10px;
}
.post_body{
    display: flex;
    flex-direction: column;
}
.post_body > .post_question{
     margin-top: 10px;
     font-weight: bold;
     margin-bottom: 10px;
     cursor: pointer;
     display: flex;
}
.post_body > .post_question > p:hover{
    text-decoration: underline;
}
.post_body > img{
    object-fit: contain;
    width: 100%;
    border-radius: 5px;
    cursor: pointer;
}
.post_question > .post_btnAnswer{
    margin-left: auto;
    outline: none;
    border: none;
    background-color: blue;
    color: white;
    border-radius: 30px;
    padding: 10px;
}
.post_footer{
    display: flex;
    align-items: center;
    margin-top: 10px;
}
.post_footer > .MuiSvgIcon-root{
    color: gray;
    margin-right: 40px;
    cursor: pointer;
}
.post_footerAction{
    display: flex;
    justify-content: space-around;
    border-radius: 33px;
    padding: 5px;
    background-color: lightgrey;
    align-items: center;
}
.post_footerAction > .MuiSvgIcon-root{
    color: gray;
    margin-right: 40px;
    cursor: pointer;
}
.post_footerAction > .MuiSvgIcon-root:hover{
color: blue;
margin-right: 40px;
cursor: pointer;
}




.post_footerLeft{
    margin-left: auto;
}
.post_footerLeft > .MuiSvgIcon-root{
    color: gray;
    cursor: pointer;
    margin-left: 30px;
}
.modal_question {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  .modal_question > h1 {
    color: blue;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  .modal_question > p {
    color: gray;
    font-size: small;
  }
  
  .modal_question > p > .name {
    color: black;
    font-weight: bold;
  }
  
  .modal_answer {
    display: flex;
    padding-top: 20px;
    flex: 1 1;
  }
  
  .modal_answer > textarea {
    width: 100%;
    height: 200px;
    padding: 5px;
    font-size: 15px;
    color: blue;
  }
  
  .modal_button {
    display: flex;
    align-items: center;
    margin-right: -20px;
    border-top: 1px solid lightgray;
  }
  
  .modal_button > .cancle {
    margin-left: auto;
    border: none;
    margin-top: 5px;
    outline: none;
    color: rgb(175, 173, 173);
    font-weight: 500;
    padding: 10px;
    margin-right: 10px;
    border-radius: 33px;
    cursor: pointer;
  }
  .modal_button > .add {
    border: none;
    outline: none;
    margin-top: 5px;
    background-color: rgb(0, 64, 255);
    color: white;
    font-weight: 700;
    padding: 10px;
    margin-right: 10px;
    border-radius: 33px;
    cursor: pointer;
  }
  .modal_button > .cancle:hover {
    color: gray;
 }
 .modal_button > .add:hover {
      color: rgb(146, 146, 238);
 }

.qHeader{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1px;
    background-color: white;
    width: 100%;
    position: sticky;
    z-index: 1000;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0,0,0,0.5);
}
.qHeader_logo > img{
    height: 40px;
    margin-right: 20px;
}
.qHeader_icons {
    display: flex;
    margin-right: 20px;

}
.qHeader_icon > .MuiSvgIcon-root{
    color: gray;
    cursor: pointer;
    font-size: xx-large;
    padding: 5px 20px;

}
.qHeader_icon > .MuiSvgIcon-root:hover{
    color: blue;
    border-radius: 10px;
    background-color: lightgray;
}
.qHeader_input{
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
    padding: 5px;
    border-radius: 5px;
}
.qHeader_input > input{
    background-color: transparent;
    outline: none;
    border: none;
    color: rgb(49,49,49);
}
.qHeader_input > .MuiSvgIcon-root{
    color: gray;
}
.qHeader_Rem{
    display: flex;
    align-items: center;
    margin-left: 25px;
}

.qHeader_Rem > .MuiSvgIcon-root {
    font-size: xx-large;
    color: gray;
    margin-left: 25px;
    cursor: pointer;
}
.qHeader_Rem > .MuiSvgIcon-root:hover {
    color: blue;
}
.qHeader_Rem > .MuiButton-root {
    color: white;
    margin-left: 25px;
    text-transform: inherit;
    background-color: blue;
    cursor: pointer;
    border-radius: 20px;
}
.qHeader_Rem > .MuiButton-root:hover {
color: white;
background-color: rgb(85, 85, 180);
}
.modal_title {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    border-bottom: 1px solid rgba(187, 187, 187, 0.5);
  }
  
  .modal_title > h5 {
    color: gray;
    font-size: 20px;
    cursor: pointer;
    font-weight: 600;
    margin-right: 30px;
  }
  
  .modal_title > h5:hover {
    color: blue;
  }
  
  .modal_info {
    display: flex;
    align-items: center;
    margin-top: 30px;
  }
  
  .modal_info > p {
    margin-left: 10px;
    font-size: small;
    color: gray;
  }
  
  .modal_info > .modal_scope {
    display: flex;
    align-items: center;
    color: rgb(98, 98, 98);
    padding: 5px;
    margin-left: 10px;
    background-color: rgb(230, 230, 230);
    border-radius: 33px;
    cursor: pointer;
  }
  .contact_info > p{
    color: lightgray;
    font-size: medium;
  
  }
  .modal_field {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    flex: 1 1;
  }
  
  .modal_field > .modal_fieldLink {
    color: gray;
    display: flex;
    /* flex-direction: column; */
    margin-top: 10px;
    align-items: center;
    border: none;
    outline: none;;
  }
  
  .modal_field > .modal_fieldLink > input {
    flex: 1 1;
    border: none;
    outline: none;
    margin-left: 5px;
  }
  
  .modal_buttons {
    display: flex;
    align-items: center;
    margin-top: 230px;
    margin-right: -20px;
    border-top: 1px solid lightgray;
  }
  
  .modal_buttons > .cancle {
    margin-left: auto;
    border: none;
    margin-top: 15px;
    outline: none;
    color: rgb(156, 154, 154);
    font-weight: 500;
    padding: 10px;
    margin-right: 10px;
    border-radius: 33px;
    cursor: pointer;
  }
  .modal_buttons > .add {
    border: none;
    outline: none;
    margin-top: 15px;
    background-color: rgb(0, 64, 255);
    color: white;
    font-weight: 700;
    padding: 10px;
    margin-right: 10px;
    border-radius: 33px;
    cursor: pointer;
  }
  .modal_buttons > .cancle:hover {
     color: gray;
  }
  .modal_buttons > .add:hover {
       color: rgb(146, 146, 238);
  }

.quoraBox{
    display: flex;
    flex-direction: column; 
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 5px;
    cursor: pointer;  
}
.quoraBox:hover {
    border: 1px solid gray;
}
.quoraBox_quora{
       display: flex;
       margin-top: 8px;
}
.quoraBox_quora > p{
     color: gray;
     font-weight: bold;
     font-size: large;
}


.quoraBox_info{
   display: flex;
   align-items: center;
}
.quoraBox_info >h5 {
     color: rgb(129,129,129);
     font-weight: 300;
     margin-left: 10px;
}
